<template>
  <div class="details full-screen">
    <pem-header ref="headerNav" left="backHome">{{ project.name }}</pem-header>
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view" :listen-scroll="listenScroll" :probeType="probeType" @scroll="scroll" v-if="!isInit">
        <div>
          <div class="details-wrapper-header" ref="detailsHeader">
            <img class="details-info-logo" :src="project.logo" @error="defaultImage"/>
            <div class="details-info-bar">
              <div class="details-info-name">{{ project.name }}</div>
              <div class="details-info-desc">
                <span v-if="project.field">{{ project.field }}</span>
                <span v-if="project.field && project.place"> | </span>
                <span v-if="project.place">{{ project.place }}</span>
              </div>
              <div class="details-info-project">
                <pem-icon name="details-round" width="26" height="26" v-if="project.round"/>
                <span v-if="project.round">{{ project.round }}</span>
                <pem-icon name="details-money" width="26" height="26" v-if="project.price"/>
                <span v-if="project.price">{{ project.price }}</span>
                <pem-icon name="details-proportion" width="26" height="26" v-if="project.proportion"/>
                <span v-if="project.proportion">{{ project.proportion }}%</span>
              </div>
            </div>
          </div>
          <pem-card title="基础信息" line size='18' v-if="project.base && project.base.list.length > 0">
            <pem-card-content :desc="project.base.desc" :list="project.base.list"></pem-card-content>
          </pem-card>
          <pem-card title="产品/服务" line size='18' v-if="project.product && project.product.list.length > 0">
            <pem-card-content :desc="project.product.desc" :list="project.product.list"></pem-card-content>
          </pem-card>
          <!-- <pem-card title="市场" line size='18' v-if="project.market && project.market.list.length > 0">
            <pem-card-content :desc="project.market.desc" :list="project.market.list"></pem-card-content>
          </pem-card> -->
          <pem-card title="团队" line size='18' v-if="project.team && project.team.list.length > 0">
            <pem-card-content :desc="project.team.desc" :list="project.team.list"></pem-card-content>
          </pem-card>
          <pem-card title="历史融资数据" line size='18' v-if="project.history && project.history.list.length > 0">
            <pem-card-content :desc="project.history.desc" :list="project.history.list" size="70"></pem-card-content>
          </pem-card>
          <div class="filler"></div>
        </div>
      </pem-scroll-view>
    </div>
    <div class="details-button">
      <pem-button text="查看BP" @submit="submit" class="submit" />
    </div>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemHeader from 'components/header/header'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemCard from 'components/card/card'
import PemIcon from 'components/pem-icon/pem-icon'
import PemButton from 'components/pem-button/pem-button'
import PemCardContent from 'components/card-content/card-content'
import PemToast from 'components/pem-toast/pem-toast'
import { WebSiteApi, ImgPath } from 'api'
import { dateFormat, setTitle } from 'common/js/util'

// 临时 后期要删除
import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'
// import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemHeader,
    PemScrollView,
    PemCard,
    PemIcon,
    PemCardContent,
    PemToast,
    PemButton
  },
  data () {
    return {
      name: '',
      isInit: true,
      project: {},
      toastText: '',
      fileUrl: null
    }
  },
  created () {
    this._setTitle('项目工场-项目详情')
    this.probeType = 3
    this.listenScroll = true
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initData () {
      this.isInit = true
      this.headerHeight = 140
      this.$refs.headerNav.setBgColor('transparent')
      this.$refs.headerNav.setFontColor('transparent')
      this.getDetail()
    },
    _setTitle (title) {
      setTitle(title)
    },
    scroll (pos) {
      if (pos.y > 0.01) {
        this.$refs.headerNav.hide()
      } else {
        this.$refs.headerNav.show()
      }
      if (pos.y >= 0) {
        this.$refs.headerNav.setBgColor('transparent')
        return false
      }
      this.$refs.headerNav.setBgColor(`rgba(41, 41, 41, ${(pos.y / this.headerHeight < -1) ? 1 : Math.abs(pos.y / this.headerHeight)})`)
      this.$refs.headerNav.setFontColor(`rgba(255, 255, 255, ${(pos.y / this.headerHeight < -1) ? 0.97 : 0})`)
    },
    submit () {
      window.open(this.fileUrl)
    },
    getDetail () {
      let _project = {}
      this._getDetail({
        zj: this.$route.params.zj.toString(),
        parentId: this.$route.query.parentid || 0,
        type: this.$route.query.type || null
      }, (data) => {
        if (+data.resultcode === -11) {
          this.toast('参数不正确')
        } else if (+data.resultcode === 1) {
          if (data.file[0].filename) {
            this.fileUrl = `${ImgPath}${data.file[0].filename}`
          }
          this.name = data.basic.name
          _project.name = data.basic.name
          _project.summary = data.basic.recommendmemo
          _project.logo = ImgPath + data.basic.logo
          _project.desc = data.basic.descriptionmemo
          _project.round = data.basic.turn
          _project.price = data.basic.currencytype + data.basic.raisefunds + '万'
          _project.place = data.basic.cityname
          let _fields = []
          if (data.field && data.field.length > 0) {
            data.field.forEach(item => {
              _fields.push(item.fieldname)
            })
          }
          _project.field = _fields.join('、')
          _project.proportion = data.basic.stockratio1
          // 基础信息
          _project.base = {
            desc: null,
            list: []
          }
          if (data.basic.highlightsmemo) {
            _project.base.list.push({
              title: '项目亮点',
              content: data.basic.highlightsmemo || '',
              type: 'text'
            })
          }
          if (data.basic.corporatename) {
            _project.base.list.push({
              title: '公司全称',
              content: data.basic.corporatename || '',
              type: 'text'
            })
          }
          if (data.basic.summarymemo) {
            _project.base.list.push({
              title: '公司简介',
              content: data.basic.summarymemo || '',
              type: 'text'
            })
          }
          if (data.basic.marketanalysis) {
            _project.base.list.push({
              title: '市场分析',
              content: data.basic.marketanalysis || '',
              type: 'text'
            })
          }
          if (data.basic.bizmodelmemo) {
            _project.base.list.push({
              title: '商业模式',
              content: data.basic.bizmodelmemo || '',
              type: 'text'
            })
          }
          if (data.basic.operatedata) {
            _project.base.list.push({
              title: '运营情况',
              content: data.basic.operatedata || '',
              type: 'text'
            })
          }
          // 基础信息 公司里程碑
          // if (data.event && data.event.length > 0) {
          //   let _content = data.event.map(item => {
          //     return {
          //       left: { title: dateFormat(item.starttime, 'yyyy-MM-dd') },
          //       right: { title: item.projectmemo }
          //     }
          //   })
          //   _project.base.list.push({
          //     title: '公司里程碑',
          //     content: _content,
          //     type: 'timeline'
          //   })
          // }
          // 产品／服务
          _project.product = {
            desc: null,
            list: []
          }
          if (data.product && data.product.length > 0) {
            data.product.forEach(item => {
              let _images = []
              if (item.archivesList && item.archivesList.length > 0) {
                item.archivesList.forEach(img => {
                  _images.push(ImgPath + img.filename)
                })
              }
              _project.product.list.push({
                title: item.prosername,
                content: item.proserintroduce || '',
                type: 'text',
                images: _images,
                urls: {
                  video: item.videolink || '',
                  website: item.officialurl || '',
                  iphone: item.iosurl || '',
                  android: item.androidurl || ''
                }
              })
            })
          }
          // if (data.project.corevaluememo) {
          //   _project.product.list.push({
          //     title: '产品核心价值',
          //     content: data.project.corevaluememo || '',
          //     type: 'text'
          //   })
          // }
          // if (data.project.bizmodelmemo) {
          //   _project.product.list.push({
          //     title: '商业模式',
          //     content: data.project.bizmodelmemo || '',
          //     type: 'text'
          //   })
          // }
          // if (data.project.comparablememo) {
          //   _project.product.list.push({
          //     title: '国际化范围内可比公司状况',
          //     content: data.project.comparablememo || '',
          //     type: 'text'
          //   })
          // }
          // 市场
          _project.market = {
            desc: null,
            list: []
          }
          if (data.project.marketchancememo) {
            _project.market.list.push({
              title: '市场机会',
              content: data.project.marketchancememo || '',
              type: 'text'
            })
          }
          if (data.project.marketpotentialmemo) {
            _project.market.list.push({
              title: '市场规模／潜力',
              content: data.project.marketpotentialmemo || '',
              type: 'text'
            })
          }
          if (data.project.keyfactormemo) {
            _project.market.list.push({
              title: '进入壁垒／关键成功要述',
              content: data.project.keyfactormemo || '',
              type: 'text'
            })
          }
          if (data.project.opponentmemo) {
            _project.market.list.push({
              title: '竞争对手分析',
              content: data.project.opponentmemo || '',
              type: 'text'
            })
          }
          if (data.project.competepowermemo) {
            _project.market.list.push({
              title: '我的竞争优势',
              content: data.project.competepowermemo || '',
              type: 'text'
            })
          }
          if (data.project.strategicplanning) {
            _project.market.list.push({
              title: '未来战略规划',
              content: data.project.strategicplanning || '',
              type: 'text'
            })
          }
          // 团队
          _project.team = {
            desc: null,
            list: []
          }
          if (data.staffs && data.staffs.length > 0) {
            _project.team.list = data.staffs.map(item => {
              return {
                username: item.name || '',
                avatar: ImgPath + item.logo,
                position: item.position || '',
                desc: item.highlightmemo || '',
                type: 'member'
              }
            }) || []
          }
          // 历史
          _project.history = {
            desc: null,
            list: []
          }
          data.histroyList = data.histroyList.filter(item => {
            return item.raisetime
          })
          if (data.histroyList && data.histroyList.length > 0) {
            let _content = data.histroyList.map(item => {
              return {
                left: {
                  title: item.turnzj || '',
                  desc: item.raisetime && dateFormat(item.raisetime || null, 'yyyy-MM-dd')
                },
                right: {
                  title: (item.raisefunds || 0) + '万元',
                  desc: item.financingside || ''
                }
              }
            }) || []
            _project.history.list.push({
              title: null,
              content: _content,
              type: 'timeline'
            })
          }
          this.project = _project
          this.isInit = false
          this.wxShare({
            title: '【' + this.project.name + '】' + this.project.summary,
            content: this.project.desc,
            imgUrl: this.project.logo,
            linkUrl: window.location.href.replace(/\?.*/, '').replace('details/', 'share?projectzj=')
          })
        }
      })
    },
    _getDetail (options, callback) {
      WebSiteApi.activityDetail(options).then(data => {
        callback(data)
      })
    },
    defaultImage (e) {
      e.target.src = tempLogoUrl
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .details
    font-size: 0.24rem
  .page-panel
    .details-wrapper-header
      width: 7.5rem
      height: 3.12rem
      bg-image(image-project-background)
      background-size: 7.5rem 3.52rem
      background-position: center bottom
      position: relative
      .details-info-logo
        width: 1.08rem
        height: 1.08rem
        border-radius: 0.08rem
        position: absolute
        left: 0.3rem
        top: 1.2rem
      .details-info-bar
        position: absolute
        top: 1.2rem
        left: 1.68rem
        .details-info-name
          color: $color-text-title-inverse
          font-size: 0.36rem
          line-height: 0.48rem
          letter-spacing: -0.0072rem
          margin-bottom: 0.1rem
        .details-info-desc
          font-size: 0.28rem
          line-height: 0.34rem
          letter-spacing: -0.0016rem
          color: $color-text-body-inverse
          margin-bottom: 0.32rem
        .details-info-project
          font-size: 0
          i, span
            vertical-align: middle
          span
            font-size: 0.24rem
            line-height: 0.36rem
            margin-right: 0.32rem
            margin-left: 0.1rem
            color: $color-text-body-inverse
  .details-button
    position: fixed
    left: 0
    right: 0
    bottom: 0
    z-index: 10
    background-color: $color-panel
    height: 1.4rem
    border-top: 0.02rem solid #f3f3f3
    .submit
      margin-top: 0.3rem
      width: 86%
</style>



